import { render } from "@testing-library/react";
import { useState } from "react";
import "./Aside.scss";
import AsideButton from "./AsideButton";
import Check from "./Check";

const Aside = () => {
  
  const [checks, updateNumOfChecks] = useState(['check'])

  const addCheck = () => {
    updateNumOfChecks(prevArray => [...prevArray, 'check'])
  }



  return (
    <aside>
      <div className="content">
        <div className="check-container">
          <h2>Check your journey</h2>

          {checks.map((check, i) => {
            return(
              <>
              <Check key={i} />
              </>
            )
            })} 

          <div className='check-button-container' onClick={addCheck} style={{display : checks.length > 2 ? 'none' : 'block'}}>
            <AsideButton buttonText="Add check" />
          </div>
        </div>

        <div className="line-container">
          <h2>Key to lines</h2>

          <div className="line-wrap">
            <div className="line"></div>
            <input type="text" placeholder="Start typing.."></input>
          </div>

          <div>
            <AsideButton className='check-button-container' buttonText="Add Line" />
          </div>
        </div>
      </div>
      <footer>
        <div>
          <p>Finished?</p>
          <p>Export</p>
        </div>
        <i class="fas fa-arrow-right"></i>
      </footer>
    </aside>
  );
};

export default Aside;
